'use strict';
import h from 'snabbdom/h'
import {
    Store, userFormChange, saveUser, deleteUser, toggleSelectUser,
    isValidUser, isValidTelephoneNumber, isValidEmail, isValidDate, isValidUserName,
    departmentsFormChange, saveDepartments, setActiveView
} from './App'


const Navigation = () => h('div.clearfix.py3', [
    h('div.sm-col.sm-col-12', [
        h('clearfix#main-nav', [
            h('div.sm-col.sm-col-4.txtcenter', [
                h('a', {
                    class: {active: Store.activeView === InfoView},
                    props: {href: "#"},
                    on: {click: ev => { ev.preventDefault(); setActiveView(InfoView); }}
                }, 'Information')
            ]),
            h('div.sm-col.sm-col-4.txtcenter', [
                h('a', {
                    class: {active: Store.activeView === DepartmentsView},
                    props: {href: "#"},
                    on: {click: ev => { ev.preventDefault(); setActiveView(DepartmentsView); }}
                }, 'Enheter/Program')
            ]),
            h('div.sm-col.sm-col-4.txtcenter', [
                h('a', {
                    class: {active: Store.activeView === UsersView},
                    props: {href: "#"},
                    on: {click: ev => { ev.preventDefault(); setActiveView(UsersView); }}
                }, 'Anvndare')
            ])
        ])
    ])
]);

const InfoView = () => h('div.clearfix.py2', [
    h('div.sm-col.sm-col-12', [
        h('clearfix', [
            h('div.sm-col.sm-col-12', [
                h('h2.info-heading', 'Fem enkla steg till steg-1 patient.'),
                h('img', {
                    props: {
                        src: "/files/sinnen.jpeg",
                        alt: 'Sinnen',
                        height: 188,
                        width: 200
                    },
                    style: {
                        float: 'left',
                        padding: '0.1em'
                    },
                }),
                h('p', '1. Fyll i dina uppgifter.'),
                h('p', '2. Vlj enhet/program dr du nskar hitta steg-1 patient.'),
                h('p', '3. Genom att klicka p spara/skicka, sparas dina uppgifter och samtidigt skickas ett automatiskt mail till de enheter/program du har valt. Det finns ven en lista med registrerade mailadresser fr de personer som kommer att f ditt mail.'),
                h('p', '4. Dina uppgifter finns kvar om du inte tar bort dem sjlv. Du ansvarar fr uppdatering och ndring av dina uppgifter. Viktigt att ta bort ditt namn frn listan om du redan hittat steg-1 patient.'),
                h('p', '5. Du kan ta kontakt med enheter/program s mnga gnger du behver, men tnk p att inte skicka alldeles fr mnga mail utan anledning!'),
                h('p', 'Lycka till!')
            ])
        ]),
        h('clearfix.py2', [h('hr')]),
        h('clearfix', [
            h('div.sm-col.sm-col-12', [
                h('h2.info-heading', 'Information till olika enheter/program inom PSV.'),
                h('h3.info-heading', 'Vilka r lmpliga KBT steg-1 patienter?'),
                h('p', '1. Patienter som har gjort klart BAS utredning.'),
                h('p', '2. Patienter som inte har ADHD, autism-spektrum strning, personlighetssyndrom. Patienter som inte har substanssyndrom.'),
                h('p', '3. Patienter som r motiverade att pbrja en behandling.'),
                h('p', '4. Patienter som kan komma, tider som passar.'),
                h('p', '5. Patienter som kan specificera ett problemomrde.'),
                h('p', '6. Lmpliga diagnoser: ngest (social fobi, GAD, panikngest, specifik fobi). Depression utan suicidrisk.'),
                h('p', '7. Patienterna betalar vanligt lkarbesk, frikort gller.'),
                h('br'),
                h('h3.info-heading', 'Praktisk information'),
                h('p', '1. Utbildningen pgr i 2 terminer, 1 r. '),
                h('p', '2. Varje ST-lkare behver genomfra 1-2 terapier under hela KBT utbildningen.'),
                h('p', '3. Ibland kan det uppst ytterligare behov under hela ret, det finns patienter som hoppar av eller inte anses vara lmpliga fr steg-1.'),
                h('p', '4. Alla sessioner spelas in (filmas ej) och handledningen pgr kontinuerligt.'),
                h('p', '5. Det gr att ha en mer komplicerad patient under utbildningsterapi, men man behver fokusera p ett problemomrde i s fall.'),
                h('p', '6. En terapi kan ta allt frn 7 sessioner upp till 20 sessioner ibland.'),
                h('p', '7. ST-lkaren fr inte ha ansvar fr mediciner, recept, sjukskrivning, intyg, remisser, mm under pgende terapi. Det mste finnas en annan patient-ansvarig-lkare (PAL) under tiden.')
            ])
        ])
    ])
]);


const UsersView = () => h('div.clearfix.py2', [
    h('div.sm-col.sm-col-12', [
        h('div.clearfix', [
            h('div.sm-col.sm-col-12', [
                h('table#users', [
                    h('thead', [
                        h('tr', [
                            h('th.left-align', 'Namn'),

                            h('th.left-align', 'Epost'),
                            h('th.left-align', 'Telefonnummer'),
                            h('th.left-align', 'nskar pbrja'),
                            h('th.left-align', 'Diagnoser'),
                        ])
                    ]),
                    h('tbody', Store.users.map(u =>
                        h('tr.user-row.pointer', {
                            class: {
                                'selected': u.email === Store.userForm.email
                            },
                            on: {
                                click: toggleSelectUser.bind(null, u)
                            }
                        }, [
                            h('td.left-align', u.name),
                            h('td.left-align', u.email),
                            h('td.left-align', u.telephone),
                            h('td.left-align', u.start),
                            h('td.left-align', u.diagnoses)
                        ])))
                ])
            ])
        ]),

        // User new/edit form

        h('div.clearfix.my4.py4', [
            h('div.sm-col.sm-col-12', [
                h('div.clearfix', [
                    h('div.sm-col.sm-col-12.h4.underline.my1', 'Anvndaruppgifter')
                ]),
                h('div.clearfix', [
                    h('div.sm-col.sm-col-12', [
                        h('input.input', {
                            class: {
                                error: Store.userForm.name !== '' && !isValidUserName(Store.userForm.name)
                            },
                            props: {
                                placeholder: 'Namn och efternamn',
                                type: 'text',
                                value: Store.userForm.name
                            },
                            on: {
                                change: userFormChange.bind(null, 'name')
                            }
                        })
                    ])
                ]),
                h('div.clearfix', [
                    h('div.sm-col.sm-col-12', [
                        h('input.input', {
                            class: {
                                error: Store.userForm.email !== '' && !isValidEmail(Store.userForm.email)
                            },
                            props: {
                                placeholder: 'Kontakt epost',
                                type: 'text',
                                value: Store.userForm.email
                            },
                            on: {
                                change: userFormChange.bind(null, 'email')
                            }
                        })
                    ])
                ]),
                h('div.clearfix', [
                    h('div.sm-col.sm-col-12', [
                        h('input.input', {
                            class: {
                                error: Store.userForm.telephone !== '' && !isValidTelephoneNumber(Store.userForm.telephone)
                            },
                            props: {
                                placeholder: 'Kontakt telefonnummer',
                                type: 'text',
                                value: Store.userForm.telephone
                            },
                            on: {
                                change: userFormChange.bind(null, 'telephone')
                            }
                        })
                    ])
                ]),
                h('div.clearfix', [
                    h('div.sm-col.sm-col-12', [
                        h('input.input', {
                            class: {
                                error: Store.userForm.need !== '' && (Store.userForm.need < 1 || Store.userForm.need > 4)
                            },
                            props: {
                                placeholder: 'Antal patienter som behvs',
                                type: 'text',
                                min: '1',
                                max: '4',
                                value: Store.userForm.need
                            },
                            on: {
                                change: userFormChange.bind(null, 'need')
                            }
                        })
                    ])
                ]),
                h('div.clearfix', [
                    h('div.sm-col.sm-col-12', [
                        h('input.input', {
                            props: {
                                placeholder: 'nskade diagnoser',
                                type: 'text',
                                value: Store.userForm.diagnoses
                            },
                            on: {
                                change: userFormChange.bind(null, 'diagnoses')
                            }
                        })
                    ])
                ]),
                h('div.clearfix', [
                    h('div.sm-col.sm-col-12', [
                        h('input.input', {
                            class: {
                                error: Store.userForm.start !== '' && !isValidDate(Store.userForm.start)
                            },
                            props: {
                                placeholder: 'nskar pbrja, datum (YYYY-MM-DD)',
                                type: 'text',
                                value: Store.userForm.start
                            },
                            on: {
                                change: userFormChange.bind(null, 'start')
                            }
                        })
                    ])
                ]),
                h('div.clearfix.mb3', [
                    h('div.sm-col.sm-col-12', [
                        h('span.six3.mr4', 'Akut behov av steg-1 patient?'),
                        h('select', {
                                on: {
                                    change: userFormChange.bind(null, 'acute')
                                }
                            }, ['ja', 'nej'].map(opt =>
                                h('option', {
                                    props: {
                                        value: opt,
                                        selected: opt === Store.userForm.acute ? 'selected' : undefined
                                    }
                                }, opt)
                            )
                        )
                    ])
                ]),
                h('div.clearfix.mb2', [
                    h('div.sm-col.sm-col-6', [
                        h('span.mr4.underline', 'Enheter som jag vill kontakta')
                    ]
                        .concat(
                            [
                                'Utredningsenheten',
                                'Afektiva programmet',
                                'Internet psykiatri',
                                'Psykosprogrammet',
                                'Affektiva mottagningen'
                            ]
                                .map(opt =>
                                    h('p', [
                                        h('input.mr1', {
                                            props: {
                                                type: 'checkbox',
                                                value: opt,
                                                checked: Store.userForm.departments.split(';').indexOf(opt) >= 0
                                            },
                                            on: {
                                                change: userFormChange.bind(null, 'departments')
                                            }
                                        }, opt),
                                        h('span.six3', opt)
                                    ]))
                        )),
                    h('div.sm-col.sm-col-6', [
                        h('img', {
                            props: {
                                src: "/files/vad.jpeg",
                                alt: "tnk-knn-gr",
                                height: 228,
                                width: 286
                            }
                        })
                    ])
                ]),
                h('div.clearfix', [
                    h('div.sm-col.sm-col-6', [
                        h('button.btn.btn-small.btn-primary.bg-green', {
                            props: {
                                disabled: !isValidUser(Store.userForm)
                            },
                            on: {
                                click: saveUser
                            }
                        }, 'Spara')
                    ]),
                    h('div.sm-col.sm-col-6', [
                        h('button.btn.btn-small.btn-primary.bg-red.right', {
                            props: {
                                disabled: !isValidUser(Store.userForm)
                            },
                            on: {
                                click: deleteUser
                            }
                        }, 'Radera')
                    ])
                ])
            ])
        ])
    ])
]);

const DepartmentsView = () => h('div.clearfix.py2', [
    // Departments
    h('div.clearfix', [
        h('div.sm-col.sm-col-12', [
            h('div.clearfix', [
                h('div.sm-col.sm-col-12.underline.h4', 'Lmpliga KBT steg-1 patienter finns p:')
            ]),
            h('div.clearfix.my2', [
                h('div.sm-col.sm-col-4.p1'),
                h('div.sm-col.sm-col-4', 'Datum'),
                h('div.sm-col.sm-col-4', 'Antal patienter'),
            ]),
            h('div.clearfix',
                Object.keys(Store.departments).map(key => {
                    const dep = Store.departments[key];

                    return h('div.clearfix', [
                        h('div.sm-col.sm-col-4.six3', dep.display),
                        h('div.sm-col.sm-col-4.six3', [
                            h('div.clearfix', [
                                h('div.sm-col.sm-col-11', [
                                    h('input.input', {
                                        props: {
                                            type: 'text',
                                            placeholder: 'YYYY-MM-DD',
                                            value: dep.date
                                        },
                                        on: {
                                            change: departmentsFormChange.bind(null, key, 'date')
                                        }
                                    })
                                ])
                            ])
                        ]),
                        h('div.sm-col.sm-col-4.six3', [
                            h('div.clearfix', [
                                h('div.sm-col.sm-col-11', [
                                    h('input.input', {
                                        props: {
                                            type: 'text',
                                            placeholder: 'antal',
                                            value: dep.number,
                                            min: '0'
                                        },
                                        on: {
                                            change: departmentsFormChange.bind(null, key, 'number')
                                        }
                                    })
                                ])
                            ])
                        ])
                    ])
                })
            ),
            h('div.clearfix', [
                h('div.sm-col.sm-col-12', [
                    h('button.btn.btn-small.btn-primary.bg-green.right', {
                        on: {
                            click: saveDepartments
                        }
                    }, 'Updatera')
                ])
            ])
        ])
    ])
]);

export { Navigation, InfoView }



